ul {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  margin: 0;
  height: 100vh;
  font-family: "Rubik", sans-serif;
  background-color: #ffffff;
}

.header {
  width: 100%;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  z-index: 1;
}

.header__logo {
  width: 9.6562rem;
  height: 3.75rem;
  margin-top: 5px;
}

.header__nav {
  width: 9.031rem;
}

.header__menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
}

.header__menu-item {
  list-style: none;
  text-transform: uppercase;
  padding: 10px 0px 13px;
}

.header__menu-link {
  color: #4a4a4a;
  padding: 5px 0px 7px;
}

.header__menu-link:hover {
  color: #14d9c4;
  border-bottom: 2px solid #14d9c4;
  transition: color,border ease 1s;
}

.main {
  height: 100vh;
  width: 100%;
  background-image: url("../images/david-van-dijk-3LTht2nxd34-unsplash.jpg");
  background-size: 120% 90%;
  color: #ffffff;
  padding: 0 20px 0 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.main__tittle {
  margin: 0;
  font-size: 1.875rem;
}

.main__tittle-items {
  display: block;
  text-transform: capitalize;
}

.footer {
  background-color: #252525;
  color: #4a4a4a;
  width: 100%;
  height: 5.625rem;
  position: fixed;
  bottom: 0;
  text-align: center;
}

.footer__copy {
  display: inline-block;
  margin: 34px 0 37px 0;
}

@media all and (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    padding: 20px 30px;
  }

  .main__tittle {
    font-size: 3.75rem;
  }

  .main {
    padding: 0 30px 0 30px;
    background-repeat: no-repeat;
  }
}

@media all and (min-width: 1024px) {
  .header {
    padding: 20px 50px;
  }

  .main {
    padding: 0 50px 0 50px;
  }
}
